<template>
  <div class="department-container">
    <!--  -->
    <header class="header">
      <div class="header-content">
        <div class="logo">
          <i class="el-icon-medal-1"></i>
          <span></span>
        </div>
        <nav class="nav-menu">
          <router-link to="/home" class="nav-item"></router-link>
          <router-link to="/departments" class="nav-item active"></router-link>
          <router-link to="/doctors" class="nav-item"></router-link>
          <router-link to="/medical-guide" class="nav-item"></router-link>
        </nav>
        <div class="user-info">
          <div v-if="user" class="logged-in">
            <span class="welcome">{{ user.name || user.username }}</span>
            <router-link to="/user-info" class="btn-text"></router-link>
            <button class="btn-text" @click="handleLogout"></button>
          </div>
          <div v-else class="not-logged-in">
            <router-link to="/login" class="btn-primary"></router-link>
            <router-link to="/register" class="btn-text"></router-link>
          </div>
        </div>
      </div>
    </header>
    <!--  -->
    <main class="main-content">
      <div class="page-header">
        <h1></h1>
        <p class="page-desc"></p>
      </div>
      <!--  -->
      <div class="search-section">
        <el-input v-model="searchKeyword" placeholder="" prefix-icon="el-icon-search" size="large">
          <template v-slot:append>
            <el-button type="primary" @click="handleSearch"></el-button>
          </template>
        </el-input>
      </div>
      <!--  -->
      <div class="department-list">
        <el-card v-for="department in filteredDepartments" :key="department.id" class="department-card">
          <div class="department-header">
            <h3 class="department-name">{{ department.name }}</h3>
            <el-tag size="medium" type="primary">{{ department.type }}</el-tag>
          </div>
          <div class="department-content">
            <p class="department-description">{{ department.description }}</p>
            <div class="department-info">
              <div class="info-item">
                <i class="el-icon-location"></i>
                <span>{{ department.location }}</span>
              </div>
              <div class="info-item">
                <i class="el-icon-phone"></i>
                <span>{{ department.phone }}</span>
              </div>
              <div class="info-item">
                <i class="el-icon-user-solid"></i>
                <span>{{ department.doctorCount || 0 }}</span>
              </div>
            </div>
          </div>
          <div class="department-footer">
            <router-link :to="{ path: '/doctors', query: { departmentId: department.id }}" class="view-doctors-btn">
              
              <i class="el-icon-arrow-right"></i>
            </router-link>
            <router-link :to="{ path: '/appointment', query: { departmentId: department.id }}" class="make-appointment-btn" v-if="user && user.role !== 'ADMIN'">
              
            </router-link>
          </div>
        </el-card>
      </div>
      <!--  -->
      <div v-if="filteredDepartments.length === 0" class="no-data">
        <el-empty description=""></el-empty>
      </div>
    </main>
    <!--  -->
    <footer class="footer">
      <div class="footer-content">
        <p> 2024  </p>
      </div>
    </footer>
  </div>
</template>

<script>
export default {
  name: 'DepartmentList',
  data() {
    return {
      user: null,
      departments: [],
      searchKeyword: '',
      loading: false
    }
  },
  computed: {
    filteredDepartments() {
      if (!this.searchKeyword.trim()) {
        return this.departments
      }
      
      const keyword = this.searchKeyword.toLowerCase().trim()
      return this.departments.filter(dept => 
        dept.name.toLowerCase().includes(keyword) || 
        dept.description.toLowerCase().includes(keyword) ||
        dept.type.toLowerCase().includes(keyword)
      )
    }
  },
  created() {
    this.loadUserInfo()
    this.loadDepartments()
  },
  methods: {
    loadUserInfo() {
      const userStr = localStorage.getItem('user')
      if (userStr) {
        this.user = JSON.parse(userStr)
      }
    },
    loadDepartments() {
      this.loading = true
      this.$axios.get('/department/list').then(res => {
        if (res.success) {
          this.departments = res.data
        }
      }).catch(err => {
        console.error(':', err)
        this.$message.error('')
      }).finally(() => {
        this.loading = false
      })
    },
    handleSearch() {
      // computed
    },
    handleLogout() {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        localStorage.removeItem('token')
        localStorage.removeItem('user')
        this.user = null
        this.$router.push('/login')
        this.$message.success('')
      }).catch(() => {
        // 
      })
    }
  }
}
</script>

