<template>
  <div class="login-container">
    <div class="login-form">
      <h2></h2>
      <el-form ref="loginForm" :model="loginForm" :rules="rules" label-width="80px">
        <el-form-item label="" prop="username">
          <el-input v-model="loginForm.username" placeholder="" prefix-icon="el-icon-user"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input type="password" v-model="loginForm.password" placeholder="" prefix-icon="el-icon-lock" show-password></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="loginForm.userType">
            <el-radio label="patient"></el-radio>
            <el-radio label="doctor"></el-radio>
            <el-radio label="admin"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleLogin" class="login-btn"></el-button>
          <el-button @click="goToRegister" class="register-btn"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data() {
    return {
      loginForm: {
        username: '',
        password: '',
        userType: 'patient'
      },
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: '6', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    handleLogin() {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          this.$axios.post('/user/login', {
            username: this.loginForm.username,
            password: this.loginForm.password,
            userType: this.loginForm.userType
          }).then(res => {
            if (res.code === 200) {
              // token
              localStorage.setItem('token', res.data.token)
              // 
              const userData = {
                username: res.data.username,
                realName: res.data.realName,
                role: res.data.roleCode.replace('ROLE_', '') // ROLE_ADMINADMIN
              }
              localStorage.setItem('user', JSON.stringify(userData))
              
              // 
              if (res.data.roleCode === 'ROLE_ADMIN') {
                this.$router.push('/admin/dashboard')
              } else {
                this.$router.push('/home')
              }
              
              this.$message.success('')
            } else {
              this.$message.error(res.message || '')
            }
          }).catch(err => {
            this.$message.error('')
            console.error(':', err)
          })
        }
      })
    },
    goToRegister() {
      this.$router.push('/register')
    }
  }
}
</script>

<style scoped>
.login-container {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f5f7fa;
}

.login-form {
  background: white;
  padding: 40px;
  border-radius: 8px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  width: 400px;
}

.login-form h2 {
  text-align: center;
  margin-bottom: 30px;
  color: #303133;
}

.login-btn {
  width: 60%;
  margin-right: 20px;
}

.register-btn {
  width: calc(40% - 20px);
}
</style>
