<script>
export default {
  name: 'Dashboard',
  data() {
    return {
      selectedDate: new Date(),
      currentTime: '',
      // 
      totalUsers: 0,
      userGrowthRate: 0,
      totalDoctors: 0,
      doctorGrowthRate: 0,
      totalDepartments: 0,
      totalAppointments: 0,
      appointmentGrowthRate: 0,
      // 
      systemUptime: '7 12',
      onlineUsers: 25,
      // 
      recentAppointments: []
    }
  },
  mounted() {
    this.initializeDashboard()
    this.startTimeUpdate()
  },
  methods: {
    initializeDashboard() {
      // 
      this.fetchStatistics()
      // 
      this.fetchRecentAppointments()
    },
    
    // 
    fetchStatistics() {
      // API
      setTimeout(() => {
        this.totalUsers = 528
        this.userGrowthRate = 3.2
        this.totalDoctors = 86
        this.doctorGrowthRate = 5.1
        this.totalDepartments = 24
        this.totalAppointments = 128
        this.appointmentGrowthRate = 8.7
      }, 500)
    },
    
    // 
    fetchRecentAppointments() {
      // API
      setTimeout(() => {
        this.recentAppointments = [
          {
            id: 1001,
            patientName: '',
            doctorName: '',
            departmentName: '',
            appointmentTime: '2024-01-20 09:30:00',
            status: ''
          },
          {
            id: 1002,
            patientName: '',
            doctorName: '',
            departmentName: '',
            appointmentTime: '2024-01-20 10:00:00',
            status: ''
          },
          {
            id: 1003,
            patientName: '',
            doctorName: '',
            departmentName: '',
            appointmentTime: '2024-01-20 10:30:00',
            status: ''
          },
          {
            id: 1004,
            patientName: '',
            doctorName: '',
            departmentName: '',
            appointmentTime: '2024-01-20 11:00:00',
            status: ''
          },
          {
            id: 1005,
            patientName: '',
            doctorName: '',
            departmentName: '',
            appointmentTime: '2024-01-20 11:30:00',
            status: ''
          }
        ]
      }, 500)
    },
    
    // 
    formatDate(dateString) {
      if (!dateString) return ''
      const date = new Date(dateString)
      return `${date.getFullYear()}-${(date.getMonth() + 1).toString().padStart(2, '0')}-${date.getDate().toString().padStart(2, '0')} ${date.getHours().toString().padStart(2, '0')}:${date.getMinutes().toString().padStart(2, '0')}`
    },
    
    // 
    getStatusType(status) {
      switch(status) {
        case '': return 'success'
        case '': return 'warning'
        case '': return 'danger'
        default: return 'info'
      }
    },
    
    // 
    handleDateChange(date) {
      console.log(':', date)
      // 
      this.fetchStatistics()
      this.fetchRecentAppointments()
    },
    
    // 
    updateCurrentTime() {
      const now = new Date()
      this.currentTime = now.toLocaleString('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit'
      })
    },
    
    // 
    startTimeUpdate() {
      this.updateCurrentTime()
      setInterval(() => {
        this.updateCurrentTime()
      }, 1000)
    },
    
    // 
    goToUserManagement() {
      this.$router.push('/admin/users')
    },
    
    // 
    goToDepartmentManagement() {
      this.$router.push('/admin/departments')
    },
    
    // 
    goToDoctorManagement() {
      this.$router.push('/admin/doctors')
    },
    
    // 
    goToAppointmentManagement() {
      this.$router.push('/admin/appointments')
    }
  }
}
</script>

