<template>
  <div class="appointment-container">
    <!--  -->
    <header class="header">
      <div class="header-content">
        <div class="logo">
          <i class="el-icon-medal-1"></i>
          <span></span>
        </div>
        <nav class="nav-menu">
          <router-link to="/home" class="nav-item"></router-link>
          <router-link to="/departments" class="nav-item"></router-link>
          <router-link to="/doctors" class="nav-item"></router-link>
          <router-link to="/medical-guide" class="nav-item"></router-link>
        </nav>
        <div class="user-info">
          <div v-if="user" class="logged-in">
            <span class="welcome">{{ user.name || user.username }}</span>
            <router-link to="/user-info" class="btn-text"></router-link>
            <button class="btn-text" @click="handleLogout"></button>
          </div>
          <div v-else class="not-logged-in">
            <router-link to="/login" class="btn-primary"></router-link>
            <router-link to="/register" class="btn-text"></router-link>
          </div>
        </div>
      </div>
    </header>
    <!--  -->
    <main class="main-content">
      <div v-if="!user">
        <el-alert
          title=""
          description=""
          type="info"
          show-icon
          :closable="false"
        >
          <router-link to="/login" slot="after-close"></router-link>
        </el-alert>
      </div>
      <div v-else>
        <div class="page-header">
          <h1></h1>
          <p class="page-desc"></p>
        </div>
        <!--  -->
        <el-form :model="appointmentForm" :rules="rules" ref="appointmentFormRef" label-width="120px" class="appointment-form">
          <el-form-item label="" prop="departmentId">
            <el-select v-model="appointmentForm.departmentId" placeholder="" @change="handleDepartmentChange">
              <el-option
                v-for="dept in departments"
                :key="dept.id"
                :label="dept.name"
                :value="dept.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="doctorId">
            <el-select v-model="appointmentForm.doctorId" placeholder="">
              <el-option
                v-for="doctor in filteredDoctors"
                :key="doctor.id"
                :label="doctor.name + ' - ' + doctor.title"
                :value="doctor.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="appointmentDate">
            <el-date-picker
              v-model="appointmentForm.appointmentDate"
              type="date"
              placeholder=""
              format="yyyy-MM-dd"
              :disabled-date="disabledDate"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="" prop="appointmentTime">
            <el-select v-model="appointmentForm.appointmentTime" placeholder="">
              <el-option
                v-for="timeSlot in availableTimeSlots"
                :key="timeSlot"
                :label="timeSlot"
                :value="timeSlot"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="symptoms">
            <el-input
              v-model="appointmentForm.symptoms"
              type="textarea"
              :rows="4"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="contactPhone"
            :rules="[{ required: true, message: '', trigger: 'blur' }, { validator: validatePhone, trigger: 'blur' }]"
          >
            <el-input v-model="appointmentForm.contactPhone" placeholder=""></el-input>
          </el-form-item>
          <el-form-item class="submit-section">
            <el-button type="primary" @click="submitForm" :loading="submitting"></el-button>
            <el-button @click="resetForm"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  name: 'Appointment',
  data() {
    return {
      user: null,
      appointmentForm: {
        departmentId: '',
        doctorId: '',
        appointmentDate: '',
        appointmentTime: '',
        symptoms: '',
        contactPhone: ''
      },
      departments: [],
      filteredDoctors: [],
      availableTimeSlots: ['', ''],
      rules: {},
      submitting: false
    }
  },
  created() {
    this.loadUserInfo()
    this.loadDepartments()
    
    // URLID
    if (this.$route.query.departmentId) {
      this.appointmentForm.departmentId = this.$route.query.departmentId
      this.handleDepartmentChange()
    }
    if (this.$route.query.doctorId) {
      this.appointmentForm.doctorId = this.$route.query.doctorId
    }
  },
  methods: {
    loadUserInfo() {
      const userStr = localStorage.getItem('user')
      if (userStr) {
        this.user = JSON.parse(userStr)
        // 
        if (this.user.phone) {
          this.appointmentForm.contactPhone = this.user.phone
        }
      }
    },
    loadDepartments() {
      this.$axios.get('/department/list').then(res => {
        if (res.success) {
          this.departments = res.data
        }
      }).catch(err => {
        console.error(':', err)
        this.$message.error('')
      })
    },
    handleDepartmentChange() {
      if (this.appointmentForm.departmentId) {
        // 
        this.appointmentForm.doctorId = ''
        // 
        this.$axios.get('/doctor/list', { 
          params: { departmentId: this.appointmentForm.departmentId } 
        }).then(res => {
          if (res.success) {
            this.filteredDoctors = res.data
          }
        }).catch(err => {
          console.error(':', err)
          this.$message.error('')
        })
      }
    },
    disabledDate(time) {
      // 
      return time.getTime() < Date.now() - 8.64e7
    },
    validatePhone(rule, value, callback) {
      // 
      const phoneReg = /^1[3-9]\d{9}$/
      if (!value) {
        callback(new Error(''))
      } else if (!phoneReg.test(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    },
    submitForm() {
      this.$refs.appointmentFormRef.validate((valid) => {
        if (valid) {
          this.submitting = true
          // 
          const submitData = {
            ...this.appointmentForm,
            userId: this.user.id
          }
          
          this.$axios.post('/appointment/create', submitData).then(res => {
            if (res.success) {
              this.$message.success('')
              this.resetForm()
              // 
              this.$router.push('/appointment-list')
            } else {
              this.$message.error(res.message || '')
            }
          }).catch(err => {
            console.error(':', err)
            this.$message.error('')
          }).finally(() => {
            this.submitting = false
          })
        }
      })
    },
    resetForm() {
      this.$refs.appointmentFormRef.resetFields()
      this.appointmentForm.doctorId = ''
      // 
      if (this.user && this.user.phone) {
        this.appointmentForm.contactPhone = this.user.phone
      }
    },
    handleLogout() {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        localStorage.removeItem('token')
        localStorage.removeItem('user')
        this.user = null
        this.$router.push('/login')
        this.$message.success('')
      }).catch(() => {
        // 
      })
    }
  }
}
</script>

<style scoped>
.appointment-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/*  */
</style>