<script>
import medicalGuideAPI from '../api/medicalGuide';

export default {
  name: 'MedicalGuide',
  data() {
    return {
      user: null,
      analyzing: false,
      analysisResult: null,
      symptomForm: {
        mainSymptom: '',
        secondarySymptoms: [],
        duration: '',
        severity: 3
      },
      commonSymptoms: [
        { label: '', value: 'headache' },
        { label: '', value: 'abdominalPain' },
        { label: '', value: 'fever' },
        { label: '', value: 'cough' },
        { label: '', value: 'chestPain' },
        { label: '', value: 'dizziness' },
        { label: '', value: 'nausea' },
        { label: '', value: 'jointPain' }
      ],
      allSymptoms: [
        { label: '', value: 'headache' },
        { label: '', value: 'dizziness' },
        { label: '', value: 'fever' },
        { label: '', value: 'cough' },
        { label: '', value: 'chestTightness' },
        { label: '', value: 'chestPain' },
        { label: '', value: 'abdominalPain' },
        { label: '', value: 'nausea' },
        { label: '', value: 'vomiting' },
        { label: '', value: 'diarrhea' },
        { label: '', value: 'constipation' },
        { label: '', value: 'jointPain' },
        { label: '', value: 'musclePain' },
        { label: '', value: 'difficultyBreathing' },
        { label: '', value: 'fatigue' },
        { label: '', value: 'insomnia' },
        { label: '', value: 'rash' },
        { label: '', value: 'blurredVision' }
      ],
      // 
      symptomToDepartment: {
        'headache': ['', ''],
        'dizziness': ['', ''],
        'fever': ['', ''],
        'cough': [''],
        'chestPain': ['', ''],
        'abdominalPain': ['', ''],
        'nausea': [''],
        'jointPain': ['', '']
      }
    }
  },
  created() {
    this.loadUserInfo()
  },
  methods: {
    loadUserInfo() {
      const userStr = localStorage.getItem('user')
      if (userStr) {
        this.user = JSON.parse(userStr)
      }
    },
    analyzeSymptoms() {
      if (!this.symptomForm.mainSymptom) {
        this.$message.warning('');
        return
      }
      
      this.analyzing = true
      
      // 
      let symptoms = this.symptomForm.mainSymptom;
      if (this.symptomForm.secondarySymptoms && this.symptomForm.secondarySymptoms.length > 0) {
        symptoms += ',' + this.symptomForm.secondarySymptoms.join(',');
      }
      
      // API
      medicalGuideAPI.getDepartmentRecommendation(symptoms)
        .then(response => {
          // 
          this.analysisResult = {
            recommendedDepartments: response.recommendedDepartments || [''],
            advice: response.advice || '',
            isEmergency: response.isEmergency || false
          }
        })
        .catch(error => {
          console.error(':', error);
          this.$message.error('');
          
          // 
          let recommendedDepartments = this.symptomToDepartment[this.symptomForm.mainSymptom] || ['']
          let advice = '';
          let isEmergency = false
          
          if (this.symptomForm.severity > 4) {
            if (this.symptomForm.mainSymptom === 'chestPain' || 
                this.symptomForm.mainSymptom === 'difficultyBreathing') {
              isEmergency = true
            }
          }
          
          this.analysisResult = {
            recommendedDepartments,
            advice,
            isEmergency
          }
        })
        .finally(() => {
          this.analyzing = false
        })
    },
    goToDepartment(deptName) {
      // 
      this.$router.push({ path: '/departments', query: { search: deptName } })
    },
    handleLogout() {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        localStorage.removeItem('token')
        localStorage.removeItem('user')
        this.user = null
        this.$router.push('/login')
        this.$message.success('')
      }).catch(() => {
        // 
      })
    }
  }
}
</script>

