<template>
  <div class="user-management">
    <div class="management-header">
      <h2></h2>
      <el-button type="primary" @click="showAddUserDialog">
        <i class="el-icon-plus"></i> 
      </el-button>
    </div>

    <!--  -->
    <div class="search-filter">
      <el-input
        v-model="searchKeyword"
        placeholder=""
        prefix-icon="el-icon-search"
        clearable
        @input="handleSearch"
        style="width: 300px; margin-right: 15px;"
      ></el-input>
      
      <el-select
        v-model="selectedRole"
        placeholder=""
        clearable
        @change="handleSearch"
        style="width: 150px; margin-right: 15px;"
      >
        <el-option label="" value="PATIENT"></el-option>
        <el-option label="" value="DOCTOR"></el-option>
        <el-option label="" value="ADMIN"></el-option>
      </el-select>
      
      <el-select
        v-model="selectedStatus"
        placeholder=""
        clearable
        @change="handleSearch"
        style="width: 150px;"
      >
        <el-option label="" :value="1"></el-option>
        <el-option label="" :value="0"></el-option>
      </el-select>
    </div>

    <!--  -->
    <div class="user-list">
      <el-table
        :data="filteredUsers"
        style="width: 100%"
        stripe
        border
        v-loading="loading"
        element-loading-text="..."
        ref="userTable"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="id" label="ID" width="80"></el-table-column>
        <el-table-column prop="username" label=""></el-table-column>
        <el-table-column prop="realName" label=""></el-table-column>
        <el-table-column prop="phone" label=""></el-table-column>
        <el-table-column prop="email" label="" show-overflow-tooltip></el-table-column>
        <el-table-column prop="role" label="">
          <template slot-scope="scope">
            <el-tag :type="getRoleType(scope.row.role)">
              {{ getRoleName(scope.row.role) }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="status" label="">
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.status"
              active-color="#67c23a"
              inactive-color="#909399"
              @change="updateUserStatus(scope.row)"
            ></el-switch>
          </template>
        </el-table-column>
        <el-table-column prop="createTime" label="" width="180">
          <template slot-scope="scope">
            {{ formatDate(scope.row.createTime) }}
          </template>
        </el-table-column>
        <el-table-column label="" width="180" fixed="right">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="showEditUserDialog(scope.row)">
              
            </el-button>
            <el-button
              type="text"
              size="small"
              @click="deleteUser(scope.row)"
              v-if="scope.row.role !== 'ADMIN'"
              style="color: #f56c6c;"
            >
              
            </el-button>
            <el-button
              type="text"
              size="small"
              @click="resetPassword(scope.row)"
              v-if="scope.row.role !== 'ADMIN'"
            >
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <div class="pagination">
        <el-pagination
          background
          layout="prev, pager, next, jumper, sizes, total"
          :total="filteredUsers.length"
          :page-size.sync="pageSize"
          :current-page.sync="currentPage"
          :page-sizes="[10, 20, 50, 100]"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        ></el-pagination>
      </div>
    </div>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addUserDialogVisible"
      width="500px"
      @close="resetAddUserForm"
    >
      <el-form
        :model="addUserForm"
        :rules="addUserRules"
        ref="addUserForm"
        label-width="100px"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="addUserForm.username" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="realName">
          <el-input v-model="addUserForm.realName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="addUserForm.phone" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addUserForm.email" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="role">
          <el-select v-model="addUserForm.role" placeholder="">
            <el-option label="" value="PATIENT"></el-option>
            <el-option label="" value="DOCTOR"></el-option>
            <el-option label="" value="ADMIN"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input
            v-model="addUserForm.password"
            type="password"
            placeholder=""
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addUserDialogVisible = false"></el-button>
        <el-button type="primary" @click="submitAddUser"></el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editUserDialogVisible"
      width="500px"
    >
      <el-form
        :model="editUserForm"
        :rules="editUserRules"
        ref="editUserForm"
        label-width="100px"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="editUserForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="realName">
          <el-input v-model="editUserForm.realName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="editUserForm.phone" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editUserForm.email" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="role" v-if="editUserForm.role !== 'ADMIN'">
          <el-select v-model="editUserForm.role" placeholder="">
            <el-option label="" value="PATIENT"></el-option>
            <el-option label="" value="DOCTOR"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" v-else>
          <el-input :value="getRoleName(editUserForm.role)" disabled></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editUserDialogVisible = false"></el-button>
        <el-button type="primary" @click="submitEditUser"></el-button>
      </div>
    </el-dialog>

    <!--  -->
    <div class="batch-operations" v-if="selectedUsers.length > 0">
      <el-tag>
         {{ selectedUsers.length }} 
      </el-tag>
      <el-button
        type="danger"
        size="small"
        @click="batchDeleteUsers"
        :disabled="selectedUsers.some(user => user.role === 'ADMIN')"
      >
        
      </el-button>
      <el-button
        type="warning"
        size="small"
        @click="batchEnableUsers"
      >
        
      </el-button>
      <el-button
        type="warning"
        size="small"
        @click="batchDisableUsers"
      >
        
      </el-button>
      <el-button size="small" @click="clearSelection"></el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'UserManagement',
  data() {
    return {
      // 
      users: [],
      filteredUsers: [],
      loading: false,
      
      // 
      searchKeyword: '',
      selectedRole: '',
      selectedStatus: '',
      
      // 
      currentPage: 1,
      pageSize: 10,
      
      // 
      selectedUsers: [],
      
      // 
      addUserDialogVisible: false,
      addUserForm: {
        username: '',
        realName: '',
        phone: '',
        email: '',
        role: 'PATIENT',
        password: ''
      },
      addUserRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 20, message: ' 3  20 ', trigger: 'blur' }
        ],
        realName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^1[3-9]\d{9}$/, message: '', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'email', message: '', trigger: 'blur' }
        ],
        role: [
          { required: true, message: '', trigger: 'change' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: ' 6 ', trigger: 'blur' }
        ]
      },
      
      // 
      editUserDialogVisible: false,
      editUserForm: {
        id: '',
        username: '',
        realName: '',
        phone: '',
        email: '',
        role: ''
      },
      editUserRules: {
        realName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^1[3-9]\d{9}$/, message: '', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'email', message: '', trigger: 'blur' }
        ],
        role: [
          { required: true, message: '', trigger: 'change' }
        ]
      }
    }
  },
  mounted() {
    this.fetchUsers()
  },
  computed: {
    // 
    paginatedUsers() {
      const start = (this.currentPage - 1) * this.pageSize
      const end = start + this.pageSize
      return this.filteredUsers.slice(start, end)
    }
  },
  methods: {
    // 
    fetchUsers() {
      this.loading = true
      // API
      setTimeout(() => {
        this.users = [
          {
            id: 1,
            username: 'admin',
            realName: '',
            phone: '13800138000',
            email: 'admin@hospital.com',
            role: 'ADMIN',
            status: 1,
            createTime: '2023-01-01 00:00:00'
          },
          {
            id: 2,
            username: 'patient1',
            realName: '',
            phone: '13800138001',
            email: 'zhangsan@example.com',
            role: 'PATIENT',
            status: 1,
            createTime: '2023-01-02 10:30:00'
          },
          {
            id: 3,
            username: 'doctor1',
            realName: '',
            phone: '13800138002',
            email: 'doctor_li@hospital.com',
            role: 'DOCTOR',
            status: 1,
            createTime: '2023-01-03 14:20:00'
          },
          {
            id: 4,
            username: 'patient2',
            realName: '',
            phone: '13800138003',
            email: 'lisi@example.com',
            role: 'PATIENT',
            status: 0,
            createTime: '2023-01-04 09:15:00'
          },
          {
            id: 5,
            username: 'doctor2',
            realName: '',
            phone: '13800138004',
            email: 'doctor_wang@hospital.com',
            role: 'DOCTOR',
            status: 1,
            createTime: '2023-01-05 16:45:00'
          },
          {
            id: 6,
            username: 'patient3',
            realName: '',
            phone: '13800138005',
            email: 'wangwu@example.com',
            role: 'PATIENT',
            status: 1,
            createTime: '2023-01-06 11:20:00'
          },
          {
            id: 7,
            username: 'patient4',
            realName: '',
            phone: '13800138006',
            email: 'zhaoliu@example.com',
            role: 'PATIENT',
            status: 1,
            createTime: '2023-01-07 13:50:00'
          },
          {
            id: 8,
            username: 'doctor3',
            realName: '',
            phone: '13800138007',
            email: 'doctor_liu@hospital.com',
            role: 'DOCTOR',
            status: 1,
            createTime: '2023-01-08 09:00:00'
          },
          {
            id: 9,
            username: 'patient5',
            realName: '',
            phone: '13800138008',
            email: 'sunqi@example.com',
            role: 'PATIENT',
            status: 0,
            createTime: '2023-01-09 15:30:00'
          },
          {
            id: 10,
            username: 'patient6',
            realName: '',
            phone: '13800138009',
            email: 'zhouba@example.com',
            role: 'PATIENT',
            status: 1,
            createTime: '2023-01-10 10:10:00'
          }
        ]
        this.filteredUsers = [...this.users]
        this.loading = false
      }, 500)
    },

    // 
    handleSearch() {
      let results = [...this.users]
      
      // 
      if (this.searchKeyword) {
        const keyword = this.searchKeyword.toLowerCase()
        results = results.filter(user => 
          user.username.toLowerCase().includes(keyword) ||
          user.realName.toLowerCase().includes(keyword) ||
          user.phone.includes(keyword)
        )
      }
      
      // 
      if (this.selectedRole) {
        results = results.filter(user => user.role === this.selectedRole)
      }
      
      // 
      if (this.selectedStatus !== '') {
        results = results.filter(user => user.status === parseInt(this.selectedStatus))
      }
      
      this.filteredUsers = results
      this.currentPage = 1 // 
    },

    // 
    getRoleName(role) {
      const roleMap = {
        'PATIENT': '',
        'DOCTOR': '',
        'ADMIN': ''
      }
      return roleMap[role] || role
    },

    // 
    getRoleType(role) {
      const typeMap = {
        'PATIENT': 'primary',
        'DOCTOR': 'success',
        'ADMIN': 'danger'
      }
      return typeMap[role] || 'info'
    },

    // 
    formatDate(dateString) {
      if (!dateString) return ''
      const date = new Date(dateString)
      return `${date.getFullYear()}-${(date.getMonth() + 1).toString().padStart(2, '0')}-${date.getDate().toString().padStart(2, '0')} ${date.getHours().toString().padStart(2, '0')}:${date.getMinutes().toString().padStart(2, '0')}:${date.getSeconds().toString().padStart(2, '0')}`
    },

    // 
    showAddUserDialog() {
      this.resetAddUserForm()
      this.addUserDialogVisible = true
    },

    // 
    resetAddUserForm() {
      this.addUserForm = {
        username: '',
        realName: '',
        phone: '',
        email: '',
        role: 'PATIENT',
        password: ''
      }
      this.$refs.addUserForm && this.$refs.addUserForm.resetFields()
    },

    // 
    submitAddUser() {
      this.$refs.addUserForm.validate((valid) => {
        if (valid) {
          // API
          const newUser = {
            id: this.users.length + 1,
            ...this.addUserForm,
            status: 1,
            createTime: new Date().toISOString().replace('T', ' ').substring(0, 19)
          }
          this.users.unshift(newUser)
          this.handleSearch() // 
          this.addUserDialogVisible = false
          this.$message.success('')
        }
      })
    },

    // 
    showEditUserDialog(user) {
      this.editUserForm = { ...user }
      this.editUserDialogVisible = true
    },

    // 
    submitEditUser() {
      this.$refs.editUserForm.validate((valid) => {
        if (valid) {
          // API
          const index = this.users.findIndex(u => u.id === this.editUserForm.id)
          if (index !== -1) {
            this.users[index] = { ...this.users[index], ...this.editUserForm }
            this.handleSearch() // 
            this.editUserDialogVisible = false
            this.$message.success('')
          }
        }
      })
    },

    // 
    updateUserStatus(user) {
      // API
      this.$message.success(`${user.status ? '' : ''}`)
    },

    // 
    deleteUser(user) {
      this.$confirm(`${user.realName}`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // API
        const index = this.users.findIndex(u => u.id === user.id)
        if (index !== -1) {
          this.users.splice(index, 1)
          this.handleSearch() // 
          this.$message.success('')
        }
      }).catch(() => {
        // 
      })
    },

    // 
    resetPassword(user) {
      this.$confirm(`${user.realName}`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // API
        this.$message.success(`${user.realName}123456`)
      }).catch(() => {
        // 
      })
    },

    // 
    batchDeleteUsers() {
      if (this.selectedUsers.some(user => user.role === 'ADMIN')) {
        this.$message.warning('')
        return
      }

      const userNames = this.selectedUsers.map(u => u.realName).join('')
      this.$confirm(`\n${userNames}`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // API
        const idsToDelete = this.selectedUsers.map(u => u.id)
        this.users = this.users.filter(u => !idsToDelete.includes(u.id))
        this.handleSearch() // 
        this.selectedUsers = []
        this.$message.success('')
      }).catch(() => {
        // 
      })
    },

    // 
    batchEnableUsers() {
      // API
      this.selectedUsers.forEach(user => {
        user.status = 1
      })
      this.selectedUsers = []
      this.$message.success('')
    },

    // 
    batchDisableUsers() {
      if (this.selectedUsers.some(user => user.role === 'ADMIN')) {
        this.$message.warning('')
        return
      }

      // API
      this.selectedUsers.forEach(user => {
        user.status = 0
      })
      this.selectedUsers = []
      this.$message.success('')
    },

    // 
    clearSelection() {
      this.$refs.userTable && this.$refs.userTable.clearSelection()
      this.selectedUsers = []
    },

    // 
    handleSizeChange(val) {
      this.pageSize = val
    },

    // 
    handleCurrentChange(val) {
      this.currentPage = val
    },

    // 
    handleSelectionChange(selection) {
      this.selectedUsers = selection
    }
  }
}
</script>

<style scoped>
