<script>
export default {
  name: 'Home',
  data() {
    return {
      user: null,
      carouselItems: [
        {
          id: 1,
          title: '',
          description: ''
        },
        {
          id: 2,
          title: '',
          description: ''
        },
        {
          id: 3,
          title: '',
          description: ''
        }
      ]
    }
  },
  created() {
    this.loadUserInfo()
  },
  methods: {
    loadUserInfo() {
      const userStr = localStorage.getItem('user')
      if (userStr) {
        this.user = JSON.parse(userStr)
      }
    },
    handleLogout() {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        localStorage.removeItem('token')
        localStorage.removeItem('user')
        this.user = null
        this.$router.push('/login')
        this.$message.success('')
      }).catch(() => {
        // 
      })
    }
  },
  // 
  watch: {
    $route: {
      handler() {
        this.loadUserInfo()
      },
      immediate: true
    }
  }
}
</script>

