<template>
  <div class="department-management">
    <div class="management-header">
      <h2></h2>
      <el-button type="primary" @click="showAddDepartmentDialog">
        <i class="el-icon-plus"></i> 
      </el-button>
    </div>

    <!--  -->
    <div class="search-filter">
      <el-input
        v-model="searchKeyword"
        placeholder=""
        prefix-icon="el-icon-search"
        clearable
        @input="handleSearch"
        style="width: 300px; margin-right: 15px;"
      ></el-input>
      
      <el-select
        v-model="selectedStatus"
        placeholder=""
        clearable
        @change="handleSearch"
        style="width: 150px;"
      >
        <el-option label="" :value="1"></el-option>
        <el-option label="" :value="0"></el-option>
      </el-select>
    </div>

    <!--  -->
    <div class="department-list">
      <el-table
        :data="filteredDepartments"
        style="width: 100%"
        stripe
        border
        v-loading="loading"
        element-loading-text="..."
        ref="departmentTable"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="id" label="ID" width="80"></el-table-column>
        <el-table-column prop="name" label="" min-width="150"></el-table-column>
        <el-table-column prop="description" label="" show-overflow-tooltip min-width="200"></el-table-column>
        <el-table-column prop="responsiblePerson" label="" min-width="120"></el-table-column>
        <el-table-column prop="phone" label="" min-width="120"></el-table-column>
        <el-table-column prop="location" label="" min-width="150"></el-table-column>
        <el-table-column prop="doctorCount" label="" width="100">
          <template slot-scope="scope">
            <el-tag type="primary" @click="viewDepartmentDoctors(scope.row)">
              {{ scope.row.doctorCount }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="status" label="">
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.status"
              active-color="#67c23a"
              inactive-color="#909399"
              @change="updateDepartmentStatus(scope.row)"
            ></el-switch>
          </template>
        </el-table-column>
        <el-table-column prop="createTime" label="" width="180">
          <template slot-scope="scope">
            {{ formatDate(scope.row.createTime) }}
          </template>
        </el-table-column>
        <el-table-column label="" width="180" fixed="right">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="showEditDepartmentDialog(scope.row)">
              
            </el-button>
            <el-button
              type="text"
              size="small"
              @click="deleteDepartment(scope.row)"
              style="color: #f56c6c;"
            >
              
            </el-button>
            <el-button
              type="text"
              size="small"
              @click="viewDepartmentDetail(scope.row)"
            >
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <div class="pagination">
        <el-pagination
          background
          layout="prev, pager, next, jumper, sizes, total"
          :total="filteredDepartments.length"
          :page-size.sync="pageSize"
          :current-page.sync="currentPage"
          :page-sizes="[10, 20, 50, 100]"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        ></el-pagination>
      </div>
    </div>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addDepartmentDialogVisible"
      width="600px"
      @close="resetAddDepartmentForm"
    >
      <el-form
        :model="addDepartmentForm"
        :rules="addDepartmentRules"
        ref="addDepartmentForm"
        label-width="120px"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="addDepartmentForm.name" placeholder="" maxlength="50"></el-input>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input
            v-model="addDepartmentForm.description"
            type="textarea"
            rows="3"
            placeholder=""
            maxlength="200"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="responsiblePerson">
          <el-input v-model="addDepartmentForm.responsiblePerson" placeholder="" maxlength="20"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="addDepartmentForm.phone" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="location">
          <el-input v-model="addDepartmentForm.location" placeholder="" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-upload
            class="avatar-uploader"
            action="#"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload"
          >
            <img v-if="addDepartmentForm.avatar" :src="addDepartmentForm.avatar" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
          <div class="upload-tip"> (jpg/png, 2MB)</div>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addDepartmentDialogVisible = false"></el-button>
        <el-button type="primary" @click="submitAddDepartment"></el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDepartmentDialogVisible"
      width="600px"
    >
      <el-form
        :model="editDepartmentForm"
        :rules="editDepartmentRules"
        ref="editDepartmentForm"
        label-width="120px"
      >
        <el-form-item label="ID">
          <el-input v-model="editDepartmentForm.id" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="editDepartmentForm.name" placeholder="" maxlength="50"></el-input>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input
            v-model="editDepartmentForm.description"
            type="textarea"
            rows="3"
            placeholder=""
            maxlength="200"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="responsiblePerson">
          <el-input v-model="editDepartmentForm.responsiblePerson" placeholder="" maxlength="20"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="editDepartmentForm.phone" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="location">
          <el-input v-model="editDepartmentForm.location" placeholder="" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-upload
            class="avatar-uploader"
            action="#"
            :show-file-list="false"
            :on-success="handleEditAvatarSuccess"
            :before-upload="beforeAvatarUpload"
          >
            <img v-if="editDepartmentForm.avatar" :src="editDepartmentForm.avatar" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
          <div class="upload-tip"> (jpg/png, 2MB)</div>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editDepartmentDialogVisible = false"></el-button>
        <el-button type="primary" @click="submitEditDepartment"></el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="departmentDetailDialogVisible"
      width="600px"
    >
      <el-descriptions :column="1" border>
        <el-descriptions-item label="ID">{{ currentDepartment.id }}</el-descriptions-item>
        <el-descriptions-item label="">{{ currentDepartment.name }}</el-descriptions-item>
        <el-descriptions-item label="">{{ currentDepartment.description }}</el-descriptions-item>
        <el-descriptions-item label="">{{ currentDepartment.responsiblePerson }}</el-descriptions-item>
        <el-descriptions-item label="">{{ currentDepartment.phone }}</el-descriptions-item>
        <el-descriptions-item label="">{{ currentDepartment.location }}</el-descriptions-item>
        <el-descriptions-item label="">{{ currentDepartment.doctorCount }}</el-descriptions-item>
        <el-descriptions-item label="">
          <el-tag :type="currentDepartment.status ? 'success' : 'danger'">
            {{ currentDepartment.status ? '' : '' }}
          </el-tag>
        </el-descriptions-item>
        <el-descriptions-item label="">{{ formatDate(currentDepartment.createTime) }}</el-descriptions-item>
        <el-descriptions-item label="" v-if="currentDepartment.avatar">
          <img :src="currentDepartment.avatar" class="detail-avatar" />
        </el-descriptions-item>
      </el-descriptions>
      <div slot="footer" class="dialog-footer">
        <el-button @click="departmentDetailDialogVisible = false"></el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="departmentDoctorsDialogVisible"
      width="800px"
    >
      <div v-if="departmentDoctors.length > 0">
        <el-table :data="departmentDoctors" stripe border>
          <el-table-column prop="id" label="ID" width="80"></el-table-column>
          <el-table-column prop="name" label=""></el-table-column>
          <el-table-column prop="title" label=""></el-table-column>
          <el-table-column prop="specialty" label=""></el-table-column>
          <el-table-column prop="phone" label=""></el-table-column>
          <el-table-column prop="status" label="">
            <template slot-scope="scope">
              <el-tag :type="scope.row.status ? 'success' : 'danger'">
                {{ scope.row.status ? '' : '' }}
              </el-tag>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div v-else class="empty-tip">
        <el-empty description=""></el-empty>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="departmentDoctorsDialogVisible = false"></el-button>
      </div>
    </el-dialog>

    <!--  -->
    <div class="batch-operations" v-if="selectedDepartments.length > 0">
      <el-tag>
         {{ selectedDepartments.length }} 
      </el-tag>
      <el-button type="danger" size="small" @click="batchDeleteDepartments">
        
      </el-button>
      <el-button type="warning" size="small" @click="batchEnableDepartments">
        
      </el-button>
      <el-button type="warning" size="small" @click="batchDisableDepartments">
        
      </el-button>
      <el-button size="small" @click="clearSelection"></el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DepartmentManagement',
  data() {
    return {
      // 
      departments: [],
      filteredDepartments: [],
      loading: false,
      
      // 
      searchKeyword: '',
      selectedStatus: '',
      
      // 
      currentPage: 1,
      pageSize: 10,
      
      // 
      selectedDepartments: [],
      
      // 
      currentDepartment: {},
      
      // 
      addDepartmentDialogVisible: false,
      addDepartmentForm: {
        name: '',
        description: '',
        responsiblePerson: '',
        phone: '',
        location: '',
        avatar: ''
      },
      addDepartmentRules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 50, message: ' 2  50 ', trigger: 'blur' }
        ],
        description: [
          { required: true, message: '', trigger: 'blur' },
          { max: 200, message: ' 200 ', trigger: 'blur' }
        ],
        responsiblePerson: [
          { required: true, message: '', trigger: 'blur' },
          { max: 20, message: ' 20 ', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^\d{3,4}-?\d{7,8}$|^1[3-9]\d{9}$/, message: '', trigger: 'blur' }
        ],
        location: [
          { required: true, message: '', trigger: 'blur' },
          { max: 100, message: ' 100 ', trigger: 'blur' }
        ]
      },
      
      // 
      editDepartmentDialogVisible: false,
      editDepartmentForm: {
        id: '',
        name: '',
        description: '',
        responsiblePerson: '',
        phone: '',
        location: '',
        avatar: ''
      },
      editDepartmentRules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 50, message: ' 2  50 ', trigger: 'blur' }
        ],
        description: [
          { required: true, message: '', trigger: 'blur' },
          { max: 200, message: ' 200 ', trigger: 'blur' }
        ],
        responsiblePerson: [
          { required: true, message: '', trigger: 'blur' },
          { max: 20, message: ' 20 ', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^\d{3,4}-?\d{7,8}$|^1[3-9]\d{9}$/, message: '', trigger: 'blur' }
        ],
        location: [
          { required: true, message: '', trigger: 'blur' },
          { max: 100, message: ' 100 ', trigger: 'blur' }
        ]
      },
      
      // 
      departmentDetailDialogVisible: false,
      
      // 
      departmentDoctorsDialogVisible: false,
      departmentDoctors: []
    }
  },
  mounted() {
    this.fetchDepartments()
  },
  computed: {
    // 
    paginatedDepartments() {
      const start = (this.currentPage - 1) * this.pageSize
      const end = start + this.pageSize
      return this.filteredDepartments.slice(start, end)
    }
  },
  methods: {
    // 
    fetchDepartments() {
      this.loading = true
      // API
      setTimeout(() => {
        this.departments = [
          {
            id: 1,
            name: '',
            description: '',
            responsiblePerson: '',
            phone: '010-12345601',
            location: '2',
            doctorCount: 15,
            status: 1,
            createTime: '2023-01-01 00:00:00',
            avatar: 'https://via.placeholder.com/150'
          },
          {
            id: 2,
            name: '',
            description: '',
            responsiblePerson: '',
            phone: '010-12345602',
            location: '3',
            doctorCount: 12,
            status: 1,
            createTime: '2023-01-01 00:00:00',
            avatar: 'https://via.placeholder.com/150'
          },
          {
            id: 3,
            name: '',
            description: '',
            responsiblePerson: '',
            phone: '010-12345603',
            location: '4',
            doctorCount: 8,
            status: 1,
            createTime: '2023-01-01 00:00:00',
            avatar: 'https://via.placeholder.com/150'
          },
          {
            id: 4,
            name: '',
            description: '',
            responsiblePerson: '',
            phone: '010-12345604',
            location: '5',
            doctorCount: 10,
            status: 1,
            createTime: '2023-01-01 00:00:00',
            avatar: 'https://via.placeholder.com/150'
          },
          {
            id: 5,
            name: '',
            description: '',
            responsiblePerson: '',
            phone: '010-12345605',
            location: '6',
            doctorCount: 5,
            status: 1,
            createTime: '2023-01-01 00:00:00',
            avatar: 'https://via.placeholder.com/150'
          },
          {
            id: 6,
            name: '',
            description: '',
            responsiblePerson: '',
            phone: '010-12345606',
            location: '1',
            doctorCount: 7,
            status: 1,
            createTime: '2023-01-01 00:00:00',
            avatar: 'https://via.placeholder.com/150'
          },
          {
            id: 7,
            name: '',
            description: '',
            responsiblePerson: '',
            phone: '010-12345607',
            location: '2',
            doctorCount: 4,
            status: 1,
            createTime: '2023-01-01 00:00:00',
            avatar: 'https://via.placeholder.com/150'
          },
          {
            id: 8,
            name: '',
            description: '',
            responsiblePerson: '',
            phone: '010-12345608',
            location: '3',
            doctorCount: 6,
            status: 1,
            createTime: '2023-01-01 00:00:00',
            avatar: 'https://via.placeholder.com/150'
          },
          {
            id: 9,
            name: '',
            description: '',
            responsiblePerson: '',
            phone: '010-12345609',
            location: '7',
            doctorCount: 3,
            status: 1,
            createTime: '2023-01-01 00:00:00',
            avatar: 'https://via.placeholder.com/150'
          },
          {
            id: 10,
            name: '',
            description: '',
            responsiblePerson: '',
            phone: '010-12345610',
            location: '8',
            doctorCount: 8,
            status: 1,
            createTime: '2023-01-01 00:00:00',
            avatar: 'https://via.placeholder.com/150'
          }
        ]
        this.filteredDepartments = [...this.departments]
        this.loading = false
      }, 500)
    },

    // 
    handleSearch() {
      let results = [...this.departments]
      
      // 
      if (this.searchKeyword) {
        const keyword = this.searchKeyword.toLowerCase()
        results = results.filter(dept => 
          dept.name.toLowerCase().includes(keyword) ||
          dept.description.toLowerCase().includes(keyword) ||
          dept.responsiblePerson.toLowerCase().includes(keyword)
        )
      }
      
      // 
      if (this.selectedStatus !== '') {
        results = results.filter(dept => dept.status === parseInt(this.selectedStatus))
      }
      
      this.filteredDepartments = results
      this.currentPage = 1 // 
    },

    // 
    formatDate(dateString) {
      if (!dateString) return ''
      const date = new Date(dateString)
      return `${date.getFullYear()}-${(date.getMonth() + 1).toString().padStart(2, '0')}-${date.getDate().toString().padStart(2, '0')} ${date.getHours().toString().padStart(2, '0')}:${date.getMinutes().toString().padStart(2, '0')}:${date.getSeconds().toString().padStart(2, '0')}`
    },

    // 
    showAddDepartmentDialog() {
      this.resetAddDepartmentForm()
      this.addDepartmentDialogVisible = true
    },

    // 
    resetAddDepartmentForm() {
      this.addDepartmentForm = {
        name: '',
        description: '',
        responsiblePerson: '',
        phone: '',
        location: '',
        avatar: ''
      }
      this.$refs.addDepartmentForm && this.$refs.addDepartmentForm.resetFields()
    },

    // 
    submitAddDepartment() {
      this.$refs.addDepartmentForm.validate((valid) => {
        if (valid) {
          // API
          const newDepartment = {
            id: this.departments.length + 1,
            ...this.addDepartmentForm,
            doctorCount: 0,
            status: 1,
            createTime: new Date().toISOString().replace('T', ' ').substring(0, 19)
          }
          this.departments.unshift(newDepartment)
          this.handleSearch() // 
          this.addDepartmentDialogVisible = false
          this.$message.success('')
        }
      })
    },

    // 
    showEditDepartmentDialog(department) {
      this.editDepartmentForm = { ...department }
      this.editDepartmentDialogVisible = true
    },

    // 
    submitEditDepartment() {
      this.$refs.editDepartmentForm.validate((valid) => {
        if (valid) {
          // API
          const index = this.departments.findIndex(d => d.id === this.editDepartmentForm.id)
          if (index !== -1) {
            // 
            const original = this.departments[index]
            this.departments[index] = { 
              ...this.editDepartmentForm,
              doctorCount: original.doctorCount,
              status: original.status,
              createTime: original.createTime
            }
            this.handleSearch() // 
            this.editDepartmentDialogVisible = false
            this.$message.success('')
          }
        }
      })
    },

    // 
    updateDepartmentStatus(department) {
      // API
      this.$message.success(`${department.status ? '' : ''}`)
    },

    // 
    deleteDepartment(department) {
      if (department.doctorCount > 0) {
        this.$message.warning('')
        return
      }
      
      this.$confirm(`${department.name}`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // API
        const index = this.departments.findIndex(d => d.id === department.id)
        if (index !== -1) {
          this.departments.splice(index, 1)
          this.handleSearch() // 
          this.$message.success('')
        }
      }).catch(() => {
        // 
      })
    },

    // 
    viewDepartmentDetail(department) {
      this.currentDepartment = { ...department }
      this.departmentDetailDialogVisible = true
    },

    // 
    viewDepartmentDoctors(department) {
      // API
      this.departmentDoctors = this.getDepartmentDoctors(department.id)
      this.departmentDoctorsDialogVisible = true
    },

    // 
    getDepartmentDoctors(departmentId) {
      const mockDoctors = {
        1: [ // 
          { id: 101, name: '', title: '', specialty: '', phone: '13800138101', status: 1 },
          { id: 102, name: '', title: '', specialty: '', phone: '13800138102', status: 1 },
          { id: 103, name: '', title: '', specialty: '', phone: '13800138103', status: 1 }
        ],
        2: [ // 
          { id: 201, name: '', title: '', specialty: '', phone: '13800138201', status: 1 },
          { id: 202, name: '', title: '', specialty: '', phone: '13800138202', status: 1 }
        ],
        3: [ // 
          { id: 301, name: '', title: '', specialty: '', phone: '13800138301', status: 1 },
          { id: 302, name: '', title: '', specialty: '', phone: '13800138302', status: 1 }
        ]
      }
      return mockDoctors[departmentId] || []
    },

    // 
    batchDeleteDepartments() {
      const departmentsWithDoctors = this.selectedDepartments.filter(d => d.doctorCount > 0)
      if (departmentsWithDoctors.length > 0) {
        const departmentNames = departmentsWithDoctors.map(d => d.name).join('')
        this.$message.warning(`${departmentNames}`)
        return
      }

      const departmentNames = this.selectedDepartments.map(d => d.name).join('')
      this.$confirm(`\n${departmentNames}`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // API
        const idsToDelete = this.selectedDepartments.map(d => d.id)
        this.departments = this.departments.filter(d => !idsToDelete.includes(d.id))
        this.handleSearch() // 
        this.selectedDepartments = []
        this.$message.success('')
      }).catch(() => {
        // 
      })
    },

    // 
    batchEnableDepartments() {
      // API
      this.selectedDepartments.forEach(department => {
        department.status = 1
      })
      this.selectedDepartments = []
      this.$message.success('')
    },

    // 
    batchDisableDepartments() {
      // API
      this.selectedDepartments.forEach(department => {
        department.status = 0
      })
      this.selectedDepartments = []
      this.$message.success('')
    },

    // 
    clearSelection() {
      this.$refs.departmentTable && this.$refs.departmentTable.clearSelection()
      this.selectedDepartments = []
    },

    // 
    handleSizeChange(val) {
      this.pageSize = val
    },

    // 
    handleCurrentChange(val) {
      this.currentPage = val
    },

    // 
    handleSelectionChange(selection) {
      this.selectedDepartments = selection
    },

    // 
    handleAvatarSuccess(res, file) {
      // 
      this.addDepartmentForm.avatar = URL.createObjectURL(file.raw)
    },

    // 
    handleEditAvatarSuccess(res, file) {
      // 
      this.editDepartmentForm.avatar = URL.createObjectURL(file.raw)
    },

    // 
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error(' JPG/PNG !')
      }
      if (!isLt2M) {
        this.$message.error(' 2MB!')
      }
      return isJPG && isLt2M
    }
  }
}
</script>

<style scoped>
.department-management {
  padding: 20px;
  background-color: #f5f7fa;
  min-height: calc(100vh - 60px);
}

.management-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.management-header h2 {
  margin: 0;
  color: #303133;
}

.search-filter {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  padding: 15px;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}

.department-list {
