<template>
  <div class="appointment-management">
    <div class="page-header">
      <h2></h2>
    </div>

    <!--  -->
    <div class="search-filter">
      <el-form :inline="true" :model="searchForm" class="search-form">
        <el-form-item label="">
          <el-input v-model="searchForm.patientName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="searchForm.doctorName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="searchForm.departmentId" placeholder="" clearable>
            <el-option v-for="dept in departments" :key="dept.id" :label="dept.name" :value="dept.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="searchForm.status" placeholder="" clearable>
            <el-option label="" value="PENDING"></el-option>
            <el-option label="" value="CONFIRMED"></el-option>
            <el-option label="" value="CANCELLED"></el-option>
            <el-option label="" value="COMPLETED"></el-option>
            <el-option label="" value="NO_SHOW"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="dateRange"
            type="daterange"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            style="width: 240px"
          ></el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSearch"></el-button>
          <el-button @click="handleReset"></el-button>
        </el-form-item>
      </el-form>
    </div>

    <!--  -->
    <div class="table-container">
      <el-table
        :data="filteredAppointments"
        style="width: 100%"
        stripe
        v-loading="loading"
      >
        <el-table-column type="selection" width="55" align="center"></el-table-column>
        <el-table-column prop="id" label="" width="120"></el-table-column>
        <el-table-column prop="patientName" label="" width="120"></el-table-column>
        <el-table-column prop="patientPhone" label="" width="150"></el-table-column>
        <el-table-column prop="departmentName" label="" width="120"></el-table-column>
        <el-table-column prop="doctorName" label="" width="120"></el-table-column>
        <el-table-column prop="appointmentDate" label="" width="120" :formatter="formatDate"></el-table-column>
        <el-table-column prop="appointmentTime" label="" width="150"></el-table-column>
        <el-table-column prop="status" label="" width="100">
          <template slot-scope="scope">
            <el-tag :type="getStatusType(scope.row.status)">{{ getStatusText(scope.row.status) }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="createTime" label="" width="180" :formatter="formatDateTime"></el-table-column>
        <el-table-column label="" width="180" fixed="right">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click="handleViewDetail(scope.row)"></el-button>
            <el-button v-if="scope.row.status === 'PENDING'" type="success" size="small" @click="handleConfirm(scope.row)"></el-button>
            <el-button v-if="['PENDING', 'CONFIRMED'].includes(scope.row.status)" type="danger" size="small" @click="handleCancel(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!--  -->
    <div class="pagination">
      <el-pagination
        background
        layout="prev, pager, next, jumper, ->, total"
        :total="filteredAppointments.length"
        :page-size="pageSize"
        :current-page="currentPage"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </div>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="detailDialogVisible"
      width="600px"
      top="10vh"
    >
      <div v-if="selectedAppointment" class="appointment-detail">
        <el-descriptions :column="1" border>
          <el-descriptions-item label="">{{ selectedAppointment.id }}</el-descriptions-item>
          <el-descriptions-item label="">
            <div>{{ selectedAppointment.patientName }} ({{ selectedAppointment.patientPhone }})</div>
            <div>{{ selectedAppointment.patientGender === 'male' ? '' : '' }}{{ selectedAppointment.patientAge }}</div>
          </el-descriptions-item>
          <el-descriptions-item label="">{{ selectedAppointment.departmentName }}</el-descriptions-item>
          <el-descriptions-item label="">{{ selectedAppointment.doctorName }}</el-descriptions-item>
          <el-descriptions-item label="">{{ formatDate(selectedAppointment.appointmentDate) }}</el-descriptions-item>
          <el-descriptions-item label="">{{ selectedAppointment.appointmentTime }}</el-descriptions-item>
          <el-descriptions-item label=""><el-tag :type="getStatusType(selectedAppointment.status)">{{ getStatusText(selectedAppointment.status) }}</el-tag></el-descriptions-item>
          <el-descriptions-item label="">{{ selectedAppointment.description || '' }}</el-descriptions-item>
          <el-descriptions-item label="">{{ formatDateTime(selectedAppointment.createTime) }}</el-descriptions-item>
          <el-descriptions-item label="">{{ formatDateTime(selectedAppointment.updateTime) }}</el-descriptions-item>
        </el-descriptions>
      </div>
    </el-dialog>

    <!--  -->
    <div class="batch-operations" v-if="selectedRows.length > 0">
      <el-button type="danger" @click="handleBatchCancel"></el-button>
      <el-button type="success" @click="handleBatchConfirm" :disabled="!canBatchConfirm"></el-button>
      <span> {{ selectedRows.length }} </span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AppointmentManagement',
  data() {
    return {
      appointments: [],
      departments: [],
      loading: false,
      searchForm: {
        patientName: '',
        doctorName: '',
        departmentId: '',
        status: ''
      },
      dateRange: [],
      currentPage: 1,
      pageSize: 10,
      detailDialogVisible: false,
      selectedAppointment: null,
      selectedRows: []
    }
  },
  computed: {
    filteredAppointments() {
      let filtered = this.appointments.filter(appointment => {
        // 
        if (this.searchForm.patientName && !appointment.patientName.toLowerCase().includes(this.searchForm.patientName.toLowerCase())) {
          return false
        }
        if (this.searchForm.doctorName && !appointment.doctorName.toLowerCase().includes(this.searchForm.doctorName.toLowerCase())) {
          return false
        }
        // 
        if (this.searchForm.departmentId && appointment.departmentId !== this.searchForm.departmentId) {
          return false
        }
        // 
        if (this.searchForm.status && appointment.status !== this.searchForm.status) {
          return false
        }
        // 
        if (this.dateRange && this.dateRange.length === 2) {
          const appointmentDate = new Date(appointment.appointmentDate)
          const startDate = new Date(this.dateRange[0])
          const endDate = new Date(this.dateRange[1])
          if (appointmentDate < startDate || appointmentDate > endDate) {
            return false
          }
        }
        return true
      })
      
      // 
      const start = (this.currentPage - 1) * this.pageSize
      const end = start + this.pageSize
      return filtered.slice(start, end)
    },
    canBatchConfirm() {
      return this.selectedRows.every(row => row.status === 'PENDING')
    }
  },
  created() {
    this.loadData()
  },
  methods: {
    loadData() {
      this.loading = true
      // 
      setTimeout(() => {
        this.appointments = this.getMockAppointments()
        this.departments = this.getMockDepartments()
        this.loading = false
      }, 500)
    },
    handleSearch() {
      this.currentPage = 1
    },
    handleReset() {
      this.searchForm = {
        patientName: '',
        doctorName: '',
        departmentId: '',
        status: ''
      }
      this.dateRange = []
      this.currentPage = 1
    },
    handleCurrentChange(page) {
      this.currentPage = page
    },
    handleViewDetail(row) {
      this.selectedAppointment = row
      this.detailDialogVisible = true
    },
    handleConfirm(row) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // 
        row.status = 'CONFIRMED'
        row.updateTime = new Date().toISOString()
        this.$message.success('')
      }).catch(() => {
        // 
      })
    },
    handleCancel(row) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // 
        row.status = 'CANCELLED'
        row.updateTime = new Date().toISOString()
        this.$message.success('')
      }).catch(() => {
        // 
      })
    },
    handleBatchCancel() {
      this.$confirm(` ${this.selectedRows.length} `, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // 
        this.selectedRows.forEach(row => {
          row.status = 'CANCELLED'
          row.updateTime = new Date().toISOString()
        })
        this.selectedRows = []
        this.$message.success('')
      }).catch(() => {
        // 
      })
    },
    handleBatchConfirm() {
      this.$confirm(` ${this.selectedRows.length} `, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // 
        this.selectedRows.forEach(row => {
          row.status = 'CONFIRMED'
          row.updateTime = new Date().toISOString()
        })
        this.selectedRows = []
        this.$message.success('')
      }).catch(() => {
        // 
      })
    },
    getStatusText(status) {
      const statusMap = {
        'PENDING': '',
        'CONFIRMED': '',
        'CANCELLED': '',
        'COMPLETED': '',
        'NO_SHOW': ''
      }
      return statusMap[status] || status
    },
    getStatusType(status) {
      const typeMap = {
        'PENDING': 'warning',
        'CONFIRMED': 'success',
        'CANCELLED': 'danger',
        'COMPLETED': 'info',
        'NO_SHOW': 'danger'
      }
      return typeMap[status] || 'default'
    },
    formatDate(date) {
      if (!date) return ''
      const d = new Date(date)
      return `${d.getFullYear()}-${String(d.getMonth() + 1).padStart(2, '0')}-${String(d.getDate()).padStart(2, '0')}`
    },
    formatDateTime(dateTime) {
      if (!dateTime) return ''
      const d = new Date(dateTime)
      return `${this.formatDate(d)} ${String(d.getHours()).padStart(2, '0')}:${String(d.getMinutes()).padStart(2, '0')}:${String(d.getSeconds()).padStart(2, '0')}`
    },
    // 
    getMockDepartments() {
      return [
        { id: '1', name: '' },
        { id: '2', name: '' },
        { id: '3', name: '' },
        { id: '4', name: '' },
        { id: '5', name: '' },
        { id: '6', name: '' },
        { id: '7', name: '' },
        { id: '8', name: '' }
      ]
    },
    getMockAppointments() {
      const mockAppointments = []
      const departments = this.getMockDepartments()
      const statuses = ['PENDING', 'CONFIRMED', 'CANCELLED', 'COMPLETED', 'NO_SHOW']
      const timeSlots = ['08:00-09:00', '09:00-10:00', '10:00-11:00', '14:00-15:00', '15:00-16:00', '16:00-17:00']
      const patientNames = ['', '', '', '', '', '', '', '']
      const doctorNames = ['', '', '', '', '', '', '', '']
      
      for (let i = 1; i <= 50; i++) {
        const randomDate = new Date()
        randomDate.setDate(randomDate.getDate() + Math.floor(Math.random() * 15) - 5)
        
        const department = departments[Math.floor(Math.random() * departments.length)]
        
        mockAppointments.push({
          id: `APT${String(i).padStart(6, '0')}`,
          patientId: `PAT${String(i).padStart(6, '0')}`,
          patientName: patientNames[Math.floor(Math.random() * patientNames.length)],
          patientPhone: `138${String(Math.floor(Math.random() * 100000000)).padStart(8, '0')}`,
          patientGender: Math.random() > 0.5 ? 'male' : 'female',
          patientAge: Math.floor(Math.random() * 80) + 18,
          doctorId: `DOC${String(Math.floor(Math.random() * 20) + 1).padStart(4, '0')}`,
          doctorName: doctorNames[Math.floor(Math.random() * doctorNames.length)],
          departmentId: department.id,
          departmentName: department.name,
          appointmentDate: randomDate.toISOString().split('T')[0],
          appointmentTime: timeSlots[Math.floor(Math.random() * timeSlots.length)],
          status: statuses[Math.floor(Math.random() * statuses.length)],
          description: Math.random() > 0.5 ? '' : '',
          createTime: new Date(randomDate.getTime() - Math.random() * 86400000 * 3).toISOString(),
          updateTime: new Date(randomDate.getTime() - Math.random() * 3600000).toISOString()
        })
      }
      
      // 
      return mockAppointments.sort((a, b) => new Date(b.appointmentDate) - new Date(a.appointmentDate))
    }
  }
}
</script>

