<template>
  <div class="register-container">
    <div class="register-form">
      <h2></h2>
      <el-form ref="registerForm" :model="registerForm" :rules="rules" label-width="100px">
        <el-form-item label="">
          <el-radio-group v-model="registerForm.userType" @change="handleUserTypeChange">
            <el-radio label="patient"></el-radio>
            <el-radio label="doctor"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="username">
          <el-input v-model="registerForm.username" placeholder="" prefix-icon="el-icon-user"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input type="password" v-model="registerForm.password" placeholder="" prefix-icon="el-icon-lock" show-password></el-input>
        </el-form-item>
        <el-form-item label="" prop="confirmPassword">
          <el-input type="password" v-model="registerForm.confirmPassword" placeholder="" prefix-icon="el-icon-lock" show-password></el-input>
        </el-form-item>
        <el-form-item label="" prop="realName">
          <el-input v-model="registerForm.realName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="registerForm.phone" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="registerForm.gender">
            <el-radio label="male"></el-radio>
            <el-radio label="female"></el-radio>
          </el-radio-group>
        </el-form-item>
        
        <!--  -->
        <div v-if="registerForm.userType === 'doctor'">
          <el-form-item label="" prop="departmentId">
            <el-select v-model="registerForm.departmentId" placeholder="">
              <el-option v-for="dept in departments" :key="dept.id" :label="dept.name" :value="dept.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="title">
            <el-input v-model="registerForm.title" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="specialty">
            <el-input v-model="registerForm.specialty" placeholder=""></el-input>
          </el-form-item>
        </div>
        
        <el-form-item>
          <el-button type="primary" @click="handleRegister" class="register-btn"></el-button>
          <el-button @click="goToLogin" class="login-btn"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Register',
  data() {
    return {
      registerForm: {
        username: '',
        password: '',
        confirmPassword: '',
        realName: '',
        phone: '',
        gender: 'male',
        userType: 'patient',
        departmentId: '',
        title: '',
        specialty: ''
      },
      departments: [],
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, max: 20, message: '4-20', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: '6', trigger: 'blur' }
        ],
        confirmPassword: [
          { required: true, message: '', trigger: 'blur' },
          { validator: this.validateConfirmPassword, trigger: 'blur' }
        ],
        realName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^1[3-9]\d{9}$/, message: '', trigger: 'blur' }
        ],
        departmentId: [
          { required: () => this.registerForm.userType === 'doctor', message: '', trigger: 'blur' }
        ],
        title: [
          { required: () => this.registerForm.userType === 'doctor', message: '', trigger: 'blur' }
        ],
        specialty: [
          { required: () => this.registerForm.userType === 'doctor', message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    // 
    this.loadDepartments()
  },
  methods: {
    loadDepartments() {
      this.$axios.get('/api/department/list').then(res => {
        if (res.code === 200) {
          this.departments = res.data
        }
      })
    },
    handleUserTypeChange() {
      // 
      this.$nextTick(() => {
        this.$refs.registerForm.clearValidate()
      })
    },
    validateConfirmPassword(rule, value, callback) {
      if (value !== this.registerForm.password) {
        callback(new Error(''))
      } else {
        callback()
      }
    },
    handleRegister() {
      this.$refs.registerForm.validate((valid) => {
        if (valid) {
          const url = this.registerForm.userType === 'doctor' 
            ? '/api/user/register-doctor' 
            : '/api/user/register'
          
          // 
          const requestData = {
            username: this.registerForm.username,
            password: this.registerForm.password,
            realName: this.registerForm.realName,
            phone: this.registerForm.phone,
            gender: this.registerForm.gender
          }
          
          // 
          if (this.registerForm.userType === 'doctor') {
            requestData.deptId = this.registerForm.departmentId
            requestData.title = this.registerForm.title
            requestData.specialty = this.registerForm.specialty
          }
          
          this.$axios.post(url, requestData).then(res => {
            if (res.code === 200) {
              this.$message.success('')
              this.$router.push('/login')
            } else {
              this.$message.error(res.message || '')
            }
          }).catch(err => {
            console.error(':', err)
            this.$message.error('')
          })
        }
      })
    },
    goToLogin() {
      this.$router.push('/login')
    }
  }
}
</script>

<style scoped>
.register-container {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f5f7fa;
}

.register-form {
  background: white;
  padding: 40px;
  border-radius: 8px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  width: 500px;
}

.register-form h2 {
  text-align: center;
  margin-bottom: 30px;
  color: #303133;
}

.register-btn {
  width: 60%;
  margin-right: 20px;
}

.login-btn {
  width: calc(40% - 20px);
}
</style>
